import { HiRefresh } from "react-icons/hi"

// pages/clients/details.js
export default function AllClientsDetailsPage() {
  const clients = [
    {
      id: 1,
      name: 'Client A',
      logo: '/logos/client1.png',
      description:
        'Client A is a leading tech solutions provider, delivering top‑notch products globally.',
      website: 'https://client-a.com',
      HiRefresh
    },
    {
      id: 2,
      name: 'Client B',
      logo: '/logos/client2.png',
      description:
        'Client B is a renowned e‑commerce brand offering quality services worldwide.',
      website: 'https://client-b.com',
    },
    {
      id: 3,
      name: 'Client C',
      logo: '/logos/client3.png',
      description:
        'Client C specializes in mobile app development and innovative platforms.',
      website: 'https://client-c.com',
    },
    {
      id: 4,
      name: 'Client D',
      logo: '/logos/client4.png',
      description:
        'Client D provides global consulting and digital transformation services.',
      website: 'https://client-d.com',
    },
    // add more clients…
  ]

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <h1 className="text-3xl font-bold mb-8 text-center">All Clients Details</h1>

      <div className="space-y-6">
        {clients.map((client) => (
          <div
            key={client.id}
            className="flex items-center bg-white rounded-xl shadow-md p-4 hover:shadow-lg transition"
          >
            {/* Logo section */}
            <div className="w-32 h-32 flex-shrink-0 mr-6">
              <img
                src={client.logo}
                alt={client.name}
                className="w-full h-full object-contain"
              />
            </div>

            {/* Details section */}
            <div className="flex-1">
              <h2 className="text-2xl font-semibold mb-2">{client.name}</h2>
              <p className="text-gray-700 mb-3">{client.description}</p>
              <a
                href={client.website}
                target="_blank"
                rel="noopener noreferrer"
                className="inline-block px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
              >
                Visit Website
              </a>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
